import React from 'react';

const About = () => (
  <section className='withLeftHeader'>
    <h1>Wicej o mnie</h1>
    <article className='verticalJustified withMargins'>
      <div>
        Mwi si, e dzie bez nauczenia si czego nowego to dzie stracony. W moim przypadku na sowach si nie koczy. Wiadomo, e istnieje niezliczona ilo frameworkw, bibliotek i narzdzi przydatnych do tworzenia front-endu. Wic mimo ju kilkuletniego stau na tym stanowisku tak naprawd staram si poszerza wiedz cay czas.
      </div>
    </article>
  </section>
)

export default About;



