import React from 'react';
import youTube from '../../images/youTube.png';

const Youtube = () => (
  <section className='withLeftHeader doubledHeader'>
    <h1>Kanaem na YouTube</h1>
    <article>
      <br />
      <div>
        Jako <a href='https://www.youtube.com/user/Kamilogram' target='_blank' rel='noopener noreferrer'>Kamilogram</a> tworz klipy z karaoke do popularnych piosenek i udostpniam je na YouTube. Ale eby si wyrni z wielu kanaw tworzcych podobny materia - aranacje brzmi jakby byy grane na pianinie!
      </div>
      <div>
        <a
          href="https://www.youtube.com/user/Kamilogram"
          target='_blank' rel='noopener noreferrer'
          title='Piano karaoke do piosenki Sylwii Grzeszczak "Tamta dziewczyna". Zobacz kana.'
          className='image100'
        >
          <img
            className='image80 smaller'
            alt='Kana Kamilogram'
            src={youTube} />
        </a>
      </div>
      <div>
        Kana idzie mi cakiem niele, mam ju w sumie prawie &nbsp; 1 300 000 &nbsp;  wywietle i ponad 2000 subskrybentw, z czego, nie ukrywam, jestem bardzo dumny.
      </div>
    </article>
  </section>
)

export default Youtube;



